*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font: normal 30px Arial;
}
.containerPrincipal{
  display: flex;
  flex-direction: column;
  height: 100vh;
}
header{
  background-color: rgb(86, 84, 84);
  display: flex;
  justify-content: space-around;
  padding: 1rem 0;
  align-items: center;
  color: white;
}
header h1{
  font-size: 1.5rem;
}
.lista{
  list-style: none;
}
.lista li{
  display: inline-block;
  /* margin: 0  1.15rem; */
  margin: 0  30px;
}
#container{
  flex-grow: 1;
  display: flex;
}
#esquerda{
background-color: red;
flex-grow: 1;
text-align: center;
}
#principal{
  background-color: blue;
  flex: 8;
  text-align: center;
}
#esquerda h2, #principal h2{
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  color: white;
}
footer{
  background-color: black;
  padding: 20px 0;
}
.containerFooter{
  color: white;
  text-align: center;
}
.img{
  display: inline-block;
  margin:10px 10px;
}

@media only  screen and (max-width:900px) {
  *{
    font-size: 20px;
  }
  
}
@media only  screen and (max-width:600px) {
  *{
    font-size: 15px;
  }
  
}